<template>
  <div>
    <div class="dropdown">
      <button
        class="btn btn-secondary dropdown-toggle"
        type="button"
        id="dropdownMenuButton2"
        data-bs-toggle="dropdown"
        aria-expanded="false"
      >
        Dropdown button
      </button>
      <ul
        class="dropdown-menu dropdown-menu-dark"
        aria-labelledby="dropdownMenuButton2"
      >
        <li><a class="dropdown-item" @click="infoval(0)">Action</a></li>
        <li><a class="dropdown-item" @click="infoval(1)">Another action</a></li>
        <li>
          <a class="dropdown-item" @click="infoval(2)">Something else here</a>
        </li>
        <li><hr class="dropdown-divider" /></li>
        <li><a class="dropdown-item" @click="infoval(3)">Separated link</a></li>
      </ul>
    </div>
    <div id="info">
      {{ info }}
    </div>
  </div>
</template>

<style>
#info {
  margin-top: 13rem;
}
</style>

<script>
export default {
  el: "#info",
  name: "if",
  data() {
    return {
      info: "",
    };
  },
  methods: {
    infoval(val) {
      switch (val) {
        case 0:
          this.info = "Action";
          break;
        case 1:
          this.info = "Another action";
          break;
        case 2:
          this.info = "Something else here";
          break;
        case 3:
          this.info = "Separated link!!!!!!!!!!!!!!!!!!!!!!!";
          break;
        default:
          break;
      }
    },
  },
  created() {
    console.log("123");
  },
};
</script>